<template>
    <v-container fluid class="pa-0 ma-0">
        <v-row justify="center" align="center">
            <v-card class="mt-8 mx-5" width="1525" elevation="0">
                <v-stepper v-model="e1">
                    <v-stepper-header>
                        <v-stepper-step step="1" :complete="step1Complete" :rules="[() => step1Complete]" color="success">Connect to Backend</v-stepper-step>
                        <v-divider></v-divider>
                        <v-stepper-step step="2" :complete="step2Complete" :rules="[() => step2Complete]" color="success">Select OPC Server</v-stepper-step>
                        <v-divider></v-divider>
                        <v-stepper-step step="3">Filter/Select Variables</v-stepper-step>
                    </v-stepper-header>
                    <v-stepper-items>
                        <!-- select backend -->
                        <v-stepper-content step="1">
                            <v-card height="65vh" class="mb-10 mx-1 mt-3" color="grey lighten-3">
                                <v-row class="px-5 pt-12" align="center" justify="center">
                                    <v-col cols="12" md="6">
                                        <v-text-field solo label="Backend Address" hint="Example: http://localhost:5000" v-model="address" :rules="urlRules" :value="BackendUrl"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" md="auto">
                                        <v-btn style="margin-top: -34px" rounded color="primary" @click="testConnection()" :loading="loading1">Test Connection</v-btn>
                                    </v-col>
                                </v-row>
                                <v-row class="px-5 pt-12" align="center" justify="center">
                                    <v-col cols="12" md="auto">
                                        <v-subheader>Backend Server located on this Device:</v-subheader>
                                    </v-col>
                                    <v-col cols="12" md="auto">
                                        <v-chip @click="address = 'http://localhost:5000'">http://localhost:5000</v-chip>
                                    </v-col>
                                </v-row>
                                <v-row class="px-5" align="center" justify="center">
                                    <v-col cols="12" md="auto">
                                        <v-subheader>Backend Server located in same Network:</v-subheader>
                                    </v-col>
                                    <v-col cols="12" md="auto">
                                        <v-chip @click="address = 'http://192.168.178.21:5000'">http://192.168.178.21:5000</v-chip>
                                    </v-col>
                                </v-row>
                                <v-row class="px-5" align="center" justify="center">
                                    <v-col cols="12" md="auto">
                                        <v-subheader>Backend Server located in other Network:</v-subheader>
                                    </v-col>
                                    <v-col cols="12" md="auto">
                                        <v-chip @click="address = 'https://example-url.com:5000'">https://example-url.com:5000</v-chip>
                                    </v-col>
                                </v-row>
                                <v-row justify="center">
                                    <v-subheader style="font-size: 10px" class="error--text">Adresses are just Examples. Real Values may differ!</v-subheader>
                                </v-row>
                            </v-card>
                            <v-row class="px-4 pb-5">
                                <v-spacer></v-spacer>
                                <v-btn color="primary" :disabled="!step1Complete" @click="e1 = 2">Next</v-btn>
                            </v-row>
                        </v-stepper-content>
                        <!-- select opc server -->
                        <v-stepper-content step="2">
                            <v-card height="65vh" class="mb-10 mx-1 mt-3" color="grey lighten-3">
                                <v-row class="px-5 pt-12" align="center" justify="center">
                                    <v-col cols="12" md="6">
                                        <v-text-field solo label="OPC Server Address" hint="Example: opc.tcp://localhost:4840" v-model="opcAddress" :rules="opcRules" :value="OpcUrl"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" md="auto">
                                        <v-btn style="margin-top: -34px" rounded color="primary" @click="testOPCConnection()" :loading="loading2">Test Connection</v-btn>
                                    </v-col>
                                </v-row>
                                <v-row class="px-5 pt-12" align="center" justify="center">
                                    <v-col cols="12" md="auto">
                                        <v-subheader>OPC Server hostet on this Device:</v-subheader>
                                    </v-col>
                                    <v-col cols="12" md="auto">
                                        <v-chip @click="opcAddress = 'opc.tcp://localhost:4840'">opc.tcp://localhost:4840</v-chip>
                                    </v-col>
                                </v-row>
                                <v-row class="px-5" align="center" justify="center">
                                    <v-col cols="12" md="auto">
                                        <v-subheader>OPC Server located in local Network:</v-subheader>
                                    </v-col>
                                    <v-col cols="12" md="auto">
                                        <v-chip @click="opcAddress = 'opc.tcp://192.168.178.52:4840'">opc.tcp://192.168.178.52:4840</v-chip>
                                    </v-col>
                                </v-row>
                                <v-row justify="center">
                                    <v-subheader style="font-size: 10px" class="error--text">Adresses are just Examples. Real Values may differ!</v-subheader>
                                </v-row>
                            </v-card>
                            <v-row class="px-4 pb-5">
                                <v-btn color="primary" outlined @click="e1 = 1"><v-icon class="mr-2">mdi-arrow-left</v-icon>previous step</v-btn>
                                <v-spacer></v-spacer>
                                <v-btn color="primary" :disabled="!step2Complete" @click="e1 = 3">Next</v-btn>
                            </v-row>
                        </v-stepper-content>
                        <!-- select opc variables -->
                        <v-stepper-content step="3">
                            <v-card height="65vh" class="mb-10 mx-1 mt-3 scroll" color="grey lighten-3">
                                <v-row class="mt-0 mx-0">
                                    <v-col cols="12" md="auto" class="pa-0">
                                        <v-navigation-drawer permanent width="400" height="65vh" class="scroll grey lighten-2">
                                            <v-treeview v-model="tree" :open="initiallyOpen" :items="items" activatable item-key="name" open-on-click>
                                                <template v-slot:prepend="{ item, open }">
                                                    <v-icon v-if="!item.file">{{ open ? 'mdi-folder-open' : 'mdi-folder' }}</v-icon>
                                                    <v-icon v-else>{{ files[item.file] }}</v-icon>
                                                </template>
                                            </v-treeview>
                                        </v-navigation-drawer>
                                    </v-col>
                                    <v-col cols="12" md="" class="pa-0">
                                        <v-card color="rgba(0,0,0,0)" elevation="0" class="pt-3">
                                            <v-row justify="center" align="center">
                                                <v-col cols="12" md="auto">
                                                    <div>test</div>
                                                </v-col>
                                            </v-row>
                                        </v-card>
                                    </v-col>
                                </v-row>
                            </v-card>
                            <v-row class="px-4 pb-5">
                                <v-btn color="primary" outlined @click="e1 = 2"><v-icon class="mr-2">mdi-arrow-left</v-icon>previous step</v-btn>
                                <v-spacer></v-spacer>
                                <!-- router push nodes -->
                                <v-btn color="primary" :disabled="!step3Complete">Continue</v-btn>
                            </v-row>
                        </v-stepper-content>
                    </v-stepper-items>
                </v-stepper>
            </v-card>
        </v-row>
    </v-container>
</template>

<script>
export default {
    name: 'Setup',
    data() {
        return {
            e1: 1,
            address: '',
            opcAddress: '',
            step1Complete: false,
            step2Complete: false,
            step3Complete: false,
            loading1: false,
            loading2: false,
            urlRules: [
                v => /^(ftp|http|https):\/\/[^ "]+$/.test(v) || 'No valid Address! Must contain http/https/ftp://...'
            ],
            opcRules: [
                v => /^(opc.tcp):\/\/[^ "]+$/.test(v) || 'No valid Address! Must contain opc.tcp://...'
            ],

            initiallyOpen: ['public'],
            files: {
                html: 'mdi-language-html5',
                js: 'mdi-nodejs',
                json: 'mdi-code-json',
                md: 'mdi-language-markdown',
                pdf: 'mdi-file-pdf',
                png: 'mdi-file-image',
                txt: 'mdi-file-document-outline',
                xls: 'mdi-file-excel',
            },
            tree: [],
            items: [
                {
                name: '.git',
                },
                {
                name: 'node_modules',
                },
                {
                name: 'public',
                children: [
                    {
                    name: 'static',
                    children: [{
                        name: 'logo.png',
                        file: 'png',
                    }],
                    },
                    {
                    name: 'favicon.ico',
                    file: 'png',
                    },
                    {
                    name: 'index.html',
                    file: 'html',
                    },
                ],
                },
                {
                name: '.gitignore',
                file: 'txt',
                },
                {
                name: 'babel.config.js',
                file: 'js',
                },
                {
                name: 'package.json',
                file: 'json',
                },
                {
                name: 'README.md',
                file: 'md',
                },
                {
                name: 'vue.config.js',
                file: 'js',
                },
                {
                name: 'yarn.lock',
                file: 'txt',
                },
            ],
        };
    },
    created() {
        this.address = this.BackendUrl
        this.opcAddress = this.OpcUrl
        if(this.BackendUrl != '') {
            this.step1Complete = true
        }
        if(this.OpcUrl != '') {
            this.step2Complete = true
        }
    },
    watch: {
        e1() {
            var snackbar = this.Snackbar
            snackbar.status = false
            this.$store.dispatch('getSnackbar', snackbar)
        }
    },
    computed: {
        // get snackbar data from store
        Snackbar() {
            return this.$store.getters.snackbar
        },
        BackendUrl() {
            return this.$store.getters.backendUrl
        },
        OpcUrl() {
            return this.$store.getters.opcUrl
        },
    },  
    methods: {
        testConnection() {
            this.loading1 = true
            this.$http.post(this.address,  { task: 'ping',
                                                    })
                    .then(response => {
                        console.log(response.status + ': ' + response.statusText)
                        this.step1Complete = true
                        this.$store.dispatch('getSnackbar', {status: true, timeout: 5000, color: 'success', btnColor: 'success', textStyle: true, outlined: true, centered: true, text: 'Successfully selected Backend Server' })
                        this.$store.dispatch('getBackendUrl', this.address)
                        this.loading1 = false
                    }, response => {
                        console.log(response.status + ': ' + response.statusText)
                        this.$store.dispatch('getSnackbar', {status: true, timeout: 5000, color: 'error', btnColor: 'error', textStyle: true, outlined: true, centered: true, text: 'No compatible Backend available under this Address' })
                        this.loading1 = false
                    });
        },
        testOPCConnection() {
            this.loading2 = true
            this.$http.post(this.BackendUrl,    {   task: 'connectUa',
                                                    url: this.opcAddress,
                                                })
                    .then(response => {
                        if(response.body == 'failed') {
                            this.$store.dispatch('getSnackbar', {status: true, timeout: 5000, color: 'error', btnColor: 'error', textStyle: true, outlined: true, centered: true, text: 'No OPC Server available under this Address!' })
                        } else {
                            this.step2Complete = true
                            this.$store.dispatch('getOpcUrl', this.opcAddress)
                            this.$store.dispatch('getSnackbar', {status: true, timeout: 5000, color: 'success', btnColor: 'success', textStyle: true, outlined: true, centered: true, text: 'Successfully selected OPC UA Server' })
                        }
                        this.loading2 = false
                    }, response => {
                        console.log(response.status + ': ' + response.statusText)
                        this.$store.dispatch('getSnackbar', {status: true, timeout: 5000, color: 'error', btnColor: 'error', textStyle: true, outlined: true, centered: true, text: 'Backend Connection failed!' })
                        this.loading2 = false
                    });
        }
    },
}
</script>

