<template>
  <v-app>
    <v-app-bar app color="primary" dark>
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"
          transition="scale-transition"
          width="40"
        />
        <div style="font-size: 30px" class="ml-3 font-weight-thin">OPC UA Client</div>
      </div>
      <v-spacer></v-spacer>
      <v-btn rounded light to="/setup" exact class="ml-3">Setup</v-btn>
      <v-icon class="mx-3">mdi-chevron-right</v-icon>
      <v-btn rounded light exact>Nodes</v-btn>
      <v-icon class="mx-3">mdi-chevron-right</v-icon>
      <v-btn rounded light to="/" exact>Control</v-btn>
    </v-app-bar>
    <v-main>
      <router-view></router-view>
    </v-main>
    <!-- global snackbar -->
    <v-snackbar v-model="Snackbar.status" :color="Snackbar.color" :timeout="Snackbar.timeout" :text="Snackbar.textStyle" :outlined="Snackbar.outlined" :centered="Snackbar.centered">
      {{ Snackbar.text }}
      <template v-slot:action="{ attrs }">
        <v-btn :color="Snackbar.btnColor" icon v-bind="attrs" @click="closeSnackbar()"><v-icon>mdi-close-circle</v-icon></v-btn>
      </template>
    </v-snackbar>
  </v-app>
</template>

<script>

export default {
  name: 'App',

  data: () => ({
    //
  }),
  watch: {
    Snackbar() {
      if(this.Snackbar.status == true) {
        setTimeout(() => this.closeSnackbar(), this.Snackbar.timeout);
      }
    },
  },
  computed: {
    // get snackbar data from store
    Snackbar() {
      return this.$store.getters.snackbar
    },
  },
  methods: {
    closeSnackbar() {
      var snackbar = this.Snackbar
      snackbar.status = false
      this.$store.dispatch('getSnackbar', snackbar)
    },
  },
};
</script>

<style>
  .scroll {
    overflow-y: auto
  }
</style>
