import Vue from 'vue';
import VueResource from 'vue-resource'

Vue.use(VueResource, {})

const state = {
    Snackbar: {},
    BackendUrl: '',
    OpcUrl: '',
};

const getters = {
    snackbar: state => {
        return state.Snackbar
    },
    backendUrl: state => {
        return state.BackendUrl
    },
    opcUrl: state => {
        return state.OpcUrl
    },
};

const actions = {
    getSnackbar({commit}, snackbarObj) {
        commit('setSnackbar', snackbarObj)
    },
    getBackendUrl({commit}, url) {
        commit('setBackendUrl', url)
    },
    getOpcUrl({commit}, url) {
        commit('setOpcUrl', url)
    },
};

const mutations = {
    setSnackbar: (state, snackbarObj) => {
        state.Snackbar = snackbarObj
    },
    setBackendUrl: (state, url) => {
        state.BackendUrl = url
    },
    setOpcUrl: (state, url) => {
        state.OpcUrl = url
    },
};

export default {
    state,
    getters,
    actions,
    mutations,
};