                <circle class="st9" style="fill:#CCCCCC;stroke:#000000;stroke-width:0.5;stroke-miterlimit:10;" cx="1000" cy="552" r="2"/>
                <circle class="st9" style="fill:#CCCCCC;stroke:#000000;stroke-width:0.5;stroke-miterlimit:10;" cx="1019" cy="556" r="2"/>
                <circle class="st9" style="fill:#CCCCCC;stroke:#000000;stroke-width:0.5;stroke-miterlimit:10;" cx="1019" cy="524" r="2"/>
                <circle class="st9" style="fill:#CCCCCC;stroke:#000000;stroke-width:0.5;stroke-miterlimit:10;" cx="981" cy="524" r="2"/>
                <circle class="st9" style="fill:#CCCCCC;stroke:#000000;stroke-width:0.5;stroke-miterlimit:10;" cx="981" cy="556" r="2"/>
                <circle class="st9" style="fill:#CCCCCC;stroke:#000000;stroke-width:0.5;stroke-miterlimit:10;" cx="988" cy="540" r="2"/>
                <circle class="st9" style="fill:#CCCCCC;stroke:#000000;stroke-width:0.5;stroke-miterlimit:10;" cx="1012" cy="540" r="2"/>
