        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn @click="uaDiscover()" outlined color="primary">Discover OPC Servers</v-btn>
          <v-btn @click="readData()" color="primary">OPC Status</v-btn>
          <v-spacer></v-spacer>
          <v-btn @click="subscribe()" outlined color="primary">Subscribe</v-btn>
        </v-card-actions>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      msg: 'Hello!',
      pos: {
        x: 0,
        y: 0,
      },
      execute: false,
    };
  },
  computed: {
    BackendUrl() {
      return this.$store.getters.backendUrl
    },
    OpcUrl() {
      return this.$store.getters.opcUrl
    },
  },
  methods: {
    drag() {
      let enableCall = true;
      this.$refs.box.addEventListener('mousemove', this.move)
      this.$refs.box.addEventListener('mousemove', e => {
        if (!enableCall) return;

        enableCall = false;
        this.getMessage(e);
        setTimeout(() => enableCall = true, 25); // delay between write requests in ms
      });
      this.execute = true
    },
    drop() {
      this.execute = false
      this.$refs.box.removeEventListener('mousemove', this.move)
      // TODO: Remove eventlistener on outer svg leave
    },
    move(event) {

      // console.log(document.querySelector("svg").getBBox().width) // 1425 - Mitte wre bei 712.5
      // console.log(event.offsetX) // range 0 bis 1425 - hellgrauer gecenterter Bereich

      // console.log(document.querySelector("svg").getBBox().height) // 990 - Mitte wre bei 495
      // console.log(event.offsetY) // range 0 bis 600 - hellgrauer gecenterter Bereich

      if((event.offsetX - 712.5) *1.64 > 350) {
        this.pos.x = 350
      } else if((event.offsetX - 712.5) *1.64 < -350) {
        this.pos.x = -350
      } else {
        this.pos.x = (event.offsetX - 712.5) *1.65
      }
      if((event.offsetY - 325) * 1.65 > 350) {
        this.pos.y = 350
      } else if((event.offsetY - 325) * 1.65 < -430) {
        this.pos.y = -430
      } else {
        this.pos.y = (event.offsetY - 325) * 1.65
      }
    },
    getMessage() {
      if(this.execute) {
        this.$http.post(this.BackendUrl,  { task:   'write_opc_data',
                                            opcUrl: this.OpcUrl,
                                            xPos:   parseInt(this.pos.x),
                                            yPos:   parseInt(this.pos.y),
                                          })
            .then(response => {
              console.log(response.body)
            });
      }
    },
    readData() {
      this.$http.post(this.BackendUrl,  { task:   'read_opc_data',
                                          opcUrl: this.OpcUrl,
                                        })
          .then(response => {
            console.log(response.body)
          });
    },
    subscribe() {
      this.$http.post(this.BackendUrl,  { task:   'subscribe',
                                          opcUrl: this.OpcUrl,
                                        })
          .then(response => {
            console.log(response.body)
          });
    },
    uaDiscover() {
      this.$http.post(this.BackendUrl,  { task:   'uaDiscover',
                                          opcUrl: this.OpcUrl,
                                        })
          .then(response => {
            console.log(JSON.parse(response.bodyText.replace(/'/g, '"')))
          });
    },
  },
}
</script>

